main{width: 70%;
margin: auto; 
border:  3px solid #63e1fe;
border-radius: 20px;
margin-top: 30px;
max-width: 850px;

}table,tbody{width: 100%;}
tr{width: 100%;
display: flex;
justify-content: space-between;
}
td{color: rgb(0, 0, 0);
background-color: rgb(255, 255, 255);
border: 3px solid #63e1fe ;
border-radius: 20px;
width: 30%;
text-align: center;
margin: 5px;
}
nav{width: 100%;}
td button{width: 100%;
border-radius: 20px;

}
input{width: 60%;
border-radius: 20px;
text-align: center;
outline: 0;
margin: 4px;
border-color: #63e1fe;
}
nav button{width: 30%;
font-size: 15px;
border-radius: 20px;
margin: 4px;
}
nav{display: flex;
justify-content: space-around;
}

@media (max-width:800px){
main{width: 85%;}

}